/*=============================
// Grid gutter
=============================*/
@use "sass:math";

.row {
  &.gutter-width {
    // Size xl
    &-xl {
      margin-left: -$space_xl * 0.5;
      margin-right: -$space_xl * 0.5;

      @media #{$media_xl} {
        margin-left: math.div(-$space_xl, 3);
        margin-right: math.div(-$space_xl, 3);
      }

      @media #{$media_sm} {
        margin-left: 0;
        margin-right: 0;
      }

      // Col
      > .col,
      > [class*="col-"] {
        padding-left: $space_xl * 0.5;
        padding-right: $space_xl * 0.5;

        @media #{$media_xl} {
          padding-left: math.div($space_xl, 3);
          padding-right: math.div($space_xl, 3);
        }

        @media #{$media_sm} {
          padding-left: 0;
          padding-right: 0;
        }
      }
    }

    // Size lg
    &-lg {
      margin-left: -$space_lg * 0.5;
      margin-right: -$space_lg * 0.5;

      @media #{$media_xl} {
        margin-left: math.div(-$space_lg, 3);
        margin-right: math.div(-$space_lg, 3);
      }

      @media #{$media_sm} {
        margin-left: 0;
        margin-right: 0;
      }

      // Col
      > .col,
      > [class*="col-"] {
        padding-left: $space_lg * 0.5;
        padding-right: $space_lg * 0.5;

        @media #{$media_xl} {
          padding-left: math.div($space_lg, 3);
          padding-right: math.div($space_lg, 3);
        }

        @media #{$media_sm} {
          padding-left: 0;
          padding-right: 0;
        }
      }
    }

    // Size md
    &-md {
      margin-left: -$space_md * 0.5;
      margin-right: -$space_md * 0.5;

      @media #{$media_xl} {
        margin-left: math.div(-$space_md, 3.3);
        margin-right: math.div(-$space_md, 3.3);
      }

      @media #{$media_sm} {
        margin-left: 0;
        margin-right: 0;
      }

      // Col
      > .col,
      > [class*="col-"] {
        padding-left: $space_md * 0.5;
        padding-right: $space_md * 0.5;

        @media #{$media_xl} {
          padding-left: math.div($space_md, 3.3);
          padding-right: math.div($space_md, 3.3);
        }

        @media #{$media_sm} {
          padding-left: 0;
          padding-right: 0;
        }
      }
    }

    // Size sm
    &-sm {
      margin-left: -$space_sm * 0.5;
      margin-right: -$space_sm * 0.5;

      @media #{$media_xl} {
        margin-left: math.div(-$space_sm, 3.3);
        margin-right: math.div(-$space_sm, 3.3);
      }

      @media #{$media_sm} {
        margin-left: 0;
        margin-right: 0;
      }

      // Col
      > .col,
      > [class*="col-"] {
        padding-left: $space_sm * 0.5;
        padding-right: $space_sm * 0.5;

        @media #{$media_xl} {
          padding-left: math.div($space_sm, 3.3);
          padding-right: math.div($space_sm, 3.3);
        }

        @media #{$media_sm} {
          padding-left: 0;
          padding-right: 0;
        }
      }
    }

    // Size xs
    &-xs {
      margin-left: -$space_xs * 0.5;
      margin-right: -$space_xs * 0.5;

      @media #{$media_xl} {
        margin-left: math.div(-$space_xs, 3.3);
        margin-right: math.div(-$space_xs, 3.3);
      }

      @media #{$media_sm} {
        margin-left: 0;
        margin-right: 0;
      }

      // Col
      > .col,
      > [class*="col-"] {
        padding-left: $space_xs * 0.5;
        padding-right: $space_xs * 0.5;

        @media #{$media_xl} {
          padding-left: math.div($space_xs, 3.3);
          padding-right: math.div($space_xs, 3.3);
        }

        @media #{$media_sm} {
          padding-left: 0;
          padding-right: 0;
        }
      }
    }
  }

  &[class*="gutter-width-"] {
    // Padding bottom
    &.with-pb-xl {
      margin-bottom: -$space_xl;

      @media #{$media_sm} {
        margin-bottom: 0;
      }

      > .col,
      > [class*="col-"] {
        padding-bottom: $space_xl;

        @media #{$media_sm} {
          &:last-child {
            padding-bottom: 0;
          }
        }
      }
    }

    &.with-pb-lg {
      margin-bottom: -$space_lg;

      @media #{$media_sm} {
