    &.with-pb-md {
      margin-bottom: -$space_md;

      @media #{$media_sm} {
        margin-bottom: 0;
      }

      > .col,
      > [class*="col-"] {
        padding-bottom: $space_md;

        @media #{$media_sm} {
          &:last-child {
            padding-bottom: 0;
          }
        }
      }
    }

    &.with-pb-sm {
      margin-bottom: -$space_sm;

      @media #{$media_sm} {
        margin-bottom: 0;
      }

      > .col,
      > [class*="col-"] {
        padding-bottom: $space_sm;

        @media #{$media_sm} {
          &:last-child {
            padding-bottom: 0;
          }
        }
      }
    }

    &.with-pb-xs {
      margin-bottom: -$space_xs;

      @media #{$media_sm} {
